.padding-img-calendar{
    padding: .5rem;
    padding-left: .7rem;
}
.form-search{
    margin-top: 1.5rem;
    margin-bottom: 1.5rem;
}
.form-search div{
    /*padding: 5px;*/
}
.icon-calendar{
    cursor: pointer;
    font-size: 1.5rem;
    color: #9B9B9B;
    height: 39px;
}
.label-form-search{
    margin-right: 5px;
    font-weight: bolder;
    color: rgba(0,0,0,.87);
    font-size: 12px;
}
.switch-form-search{
    margin-right: 5px;
}
.typeheadPraticiensBox{
    /*display: inline-block;*/
    /*width: 300px;*/
}

.zone{
    padding-bottom: 20px;
}

.fc-prev-button.fc-button.fc-button-primary, .fc-next-button.fc-button.fc-button-primary{
    background-color: #9B9B9B;
    border-color: #9B9B9B;
    display: inline-table;
}

.fc-toolbar-title{
    display: inline-table;
    margin-left: 10px !important;
    margin-right: 10px !important;
}

.fc-resourceTimelineWeek-button.fc-button.fc-button-primary, .fc-resourceTimelineMonth-button.fc-button.fc-button-primary,
.fc-resourceTimelineDay-button.fc-button.fc-button-primary{
    background-color: #9B9B9B;
    border-color: #9B9B9B;
}

.fc-resource-timeline-divider.fc-cell-shaded{
    background-color: #5D9AD4;
    border: 1px solid #5D9AD4;
}

.fc .fc-timeline-header-row-chrono .fc-timeline-slot-frame {
    justify-content: center;
}

.fc-timeline-body-expandrows .fc-timeline-lane-frame{
    display: flex;
    align-items: center;
}
.fc .fc-timeline-slot-cushion{
    color:#5D9AD4;
    padding: 0;
}
.headerDivision{
    color: #464646;
    font-weight: normal;
}
.fc .fc-timeline-overlap-enabled .fc-timeline-lane-frame .fc-timeline-events {
    padding: 0;
}
.fc-timeline-lane-frame {
    display: flex;
    align-items: center;
}
a:hover {
    color: #0056b3;
    text-decoration: none;
}

.fc .fc-button-primary:disabled {
    background-color: var(--fc-button-bg-color, #464646) !important;
    border-color: var(--fc-button-border-color, #464646) !important;
    opacity: 1;
}

.fc .fc-button-primary {
    background-color: var(--fc-button-bg-color, #9B9B9B) !important;
    border-color: var(--fc-button-bg-color, #9B9B9B) !important;
}

.fc .fc-button-primary:not(:disabled):active, .fc .fc-button-primary:not(:disabled).fc-button-active{
    background-color: var(--fc-button-active-bg-color, #464646) !important;
}

.fc .fc-button-primary{
    background-color: var(--fc-button-bg-color, #464646);
}

.conge{
    background: red;
    font: var(--unnamed-font-style-normal) normal bold 46px/47px var(--unnamed-font-family-arial);
    color: var(--unnamed-color-464646);
    text-align: center;
    font: normal normal 600 19px/21px Arial;
    letter-spacing: -0.46px;
    color: #252525;
    text-transform: uppercase;
    opacity: 1;
}
.fc-h-event:has(.fc-event-main):has(.conge) {
    border: red;
    background: red;
}
.fc-toolbar-chunk:nth-child(2) div{
    display: flex;
    align-items: center;
}

.content-event-align-vert{
    display: block;
    align-items: center;
    white-space: pre;
    text-overflow: clip;
    overflow: hidden;
}

.noDisplay {
    display: none;
}
.react-datepicker-popper {
    z-index: 10;
}

.progress-bar {
    background-color: #5D9AD4;
}

.loading{
    background-color: rgba(195, 193, 193, 0.27);
    text-align: center;
    padding: 15em;
    /*position: absolute;*/
}

.resource_p{
    width: 100%;
    overflow: auto;
    margin-bottom: 0;
    white-space: normal;
}
